.wp-block-outermost-social-share-links .outermost-social-share-link {
  line-height: 0;
}
.wp-block-outermost-social-share-links .outermost-social-share-link button {
  font-size: inherit;
  color: currentColor;
  height: auto;
  line-height: 0;
  padding: 0.25em;
}

.wp-block-outermost-social-share-links.is-style-pill-shape .outermost-social-share-link button {
  padding-left: 0.6666666667em;
  padding-right: 0.6666666667em;
}

.wp-block-outermost-social-share-links.is-style-logos-only .outermost-social-share-link button {
  padding: 0;
}

.outermost-social-share-link:hover {
  transform: none;
}

.editor-styles-wrapper .wp-block-outermost-social-share-links {
  padding: 0;
}

.wp-block-outermost-social-share-links__social-placeholder {
  display: flex;
  opacity: 0.8;
  list-style: none;
}
.wp-block-outermost-social-share-links__social-placeholder > .outermost-social-share-link {
  padding-left: 0 !important;
  margin-left: 0 !important;
  padding-right: 0 !important;
  margin-right: 0 !important;
  width: 0 !important;
  visibility: hidden;
}
.wp-block-outermost-social-share-links__social-placeholder > .wp-block-outermost-social-share-links__social-placeholder-icons {
  display: flex;
}
.wp-block-outermost-social-share-links__social-placeholder .outermost-social-share-link {
  padding: 0.25em;
}
.is-style-pill-shape .wp-block-outermost-social-share-links__social-placeholder .outermost-social-share-link {
  padding-left: 0.6666666667em;
  padding-right: 0.6666666667em;
}
.is-style-logos-only .wp-block-outermost-social-share-links__social-placeholder .outermost-social-share-link {
  padding: 0;
}
.wp-block-outermost-social-share-links__social-placeholder .outermost-social-share-link::before {
  content: "";
  display: block;
  width: 1em;
  height: 1em;
}
.is-style-logos-only .wp-block-outermost-social-share-links__social-placeholder .outermost-social-share-link::before {
  background: currentColor;
}

.wp-block-outermost-social-share-links .wp-block-outermost-social-share-links__social-prompt {
  cursor: default;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 13px;
  line-height: 24px;
  list-style: none;
  margin-top: auto;
  margin-bottom: auto;
  min-height: 24px;
  order: 2;
  padding-right: 8px;
}

.wp-block[data-align=center] > .wp-block-outermost-social-share-links {
  justify-content: center;
}

.block-editor-block-preview__content .components-button:disabled {
  opacity: 1;
}

.outermost-social-share-link.outermost-social-share-link__is-incomplete {
  opacity: 0.5;
}

.wp-block-outermost-social-share-links .is-selected .outermost-social-share-link__is-incomplete,
.outermost-social-share-link.outermost-social-share-link__is-incomplete:hover,
.outermost-social-share-link.outermost-social-share-link__is-incomplete:focus {
  opacity: 1;
}